.coding-game {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.game-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.level-indicator {
    background-color: #6a1b9a;
    color: white;
    padding: 8px 16px;
    border-radius: 20px;
}

.game-container {
    display: flex;
    gap: 20px;
}

.game-board {
    width: 400px; /* 8 cells * 50px */
    height: 400px;
    background-color: #f5f5f5;
    border: 2px solid #ddd;
    position: relative;
    display: grid;
    grid-template-rows: repeat(8, 1fr);
}

.grid-row {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
}

.grid-cell {
    width: 50px;
    height: 50px;
    border: 1px solid #ddd;
    box-sizing: border-box;
}

.grid-cell.path {
    background-color: #e3f2fd;
    border: 1px solid #90caf9;
}

.grid-cell.empty {
    background-color: #f5f5f5;
}

.character {
    position: absolute;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    transition: all 0.3s ease;
    z-index: 2;
}

.target {
    position: absolute;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    z-index: 1;
}

/* เพิ่มเอฟเฟกต์สำหรับเส้นทาง */
.grid-cell.path {
    background: linear-gradient(45deg, #e3f2fd 25%, #bbdefb 25%, #bbdefb 50%, #e3f2fd 50%, #e3f2fd 75%, #bbdefb 75%);
    background-size: 20px 20px;
    animation: pathAnimation 1s linear infinite;
}

@keyframes pathAnimation {
    from {
        background-position: 0 0;
    }
    to {
        background-position: 20px 20px;
    }
}

/* ปรับปรุงสไตล์อื่นๆ */
.character::before {
    content: '';
    position: absolute;
    width: 40px;
    height: 40px;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 50%;
    z-index: -1;
}

.target::before {
    content: '';
    position: absolute;
    width: 40px;
    height: 40px;
    background: rgba(255, 235, 59, 0.3);
    border-radius: 50%;
    animation: pulse 2s infinite;
}

@keyframes pulse {
    0% {
        transform: scale(1);
        opacity: 0.3;
    }
    50% {
        transform: scale(1.2);
        opacity: 0.5;
    }
    100% {
        transform: scale(1);
        opacity: 0.3;
    }
}

.command-panel {
    flex: 1;
    background-color: #f0f0f0;
    padding: 20px;
    border-radius: 8px;
}

.command-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.middle-buttons {
    display: flex;
    gap: 10px;
    margin: 10px 0;
}

.direction-btn {
    width: 120px;
    padding: 10px;
    font-size: 16px;
    border: 2px solid #6a1b9a;
    border-radius: 8px;
    background: white;
    color: #6a1b9a;
    cursor: pointer;
    transition: all 0.3s ease;
}

.direction-btn:hover {
    background: #6a1b9a;
    color: white;
}

.direction-btn:disabled {
    background: #ccc;
    border-color: #999;
    color: #666;
    cursor: not-allowed;
}

.command-buttons button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

.command-list {
    min-height: 200px;
    background-color: white;
    border: 1px solid #ddd;
    padding: 10px;
    margin-bottom: 20px;
}

.command-item {
    padding: 5px 10px;
    background-color: #e1bee7;
    margin: 5px 0;
    border-radius: 3px;
}

.control-buttons {
    display: flex;
    gap: 10px;
}

.run-button {
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
}

.run-button:disabled {
    background-color: #ccc;
}